import Head from "next/head";
import Icon from "../helper/Icon";
export default () => (
  <ex>
    <header>  
    <div className="logo">
1
    </div>
    <Icon name="price" width={35} />
    <Icon name="find" width={35} />
    <Icon name="basket"  width={35}/>
    <Icon name="order"  width={35}/>
    <Icon name="card" width={35} />
    </header>
    <style jsx>{`
    header   {
        display: flex;
        justify-content: center;
        background-color:red;
        width:33%;
        margin: auto;
        border-radius: 5px;
      }
    `}</style>
  </ex>
);



